.modal {
  width: 375px;
  height: 220px;

  border-radius: 5px 5px 0px 0px;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  background: #fff;
  z-index: 10;
}

.modal-dimmed {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;

  background: rgba(0, 0, 0, 0.5);

  z-index: 5;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.colorbox-grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 25px;
}
